#sec1 {
  position: relative;
  overflow: hidden;
}

#sec1 video {
  width:100%;
}

#sec1 .textwrap.appearActive {
  transform:translate(-50%,-50%);
}
#sec1 .textwrap.appear {
  transform:translate(-50%,0%);
}

#sec3 {
  height:90vh;
  background:url("./b2.png") no-repeat 50% 50%;
  background-size:cover;
}

#sec3 .textwrap {
  width:60%;
  text-align: center;
}

#sec3 .textwrap figure {
  background: #100f0f;
  color:white;
  display: inline-block;
  font-size:4rem;
  width:150px;
  height:150px;
  text-align: center;
  line-height: 150px;
  position: relative;
  border-radius:20px;
  margin:3rem 1rem;
}
#sec3 .textwrap figure img {
  width:70%;
  position: absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
}
#sec3 .textwrap figure figcaption {
  width:300%;
  color:black;
  font-size:1.5rem;
  position: absolute;
  bottom:-40%;
  left:50%;
  transform:translate(-50%);
  line-height:normal;
}
span.hidden {
  opacity:0;
}
