#sec2 {
  background: #f2f2f2;
  padding:5rem 0;
}
#sec2 .slider {
  width:60%;
  margin:0rem auto;
  position: relative;
}
#sec2 .slider h2 {
  margin-bottom:3rem;
  text-align: center;
}
#sec2 .slider ul {
  position: relative;
  font-size:0;
  box-shadow:0 5px 10px #cccccc;
}
#sec2 .slider ul li {
  display: inline-block;
  transition:opacity .5s ease;
  opacity:0;
  position: relative;
  position: absolute;
  width:100%;
}
#sec2 .slider ul li.imgActive {
  opacity:1;
}
#sec2 .slider ul li img {
  width:100%;
}
#sec2 .slider .buttons {
  text-align: center;
  margin-top:1.5rem;
  position: absolute;
  top:50%;
  right:0%;
  transform:translate(300%,-50%);
  z-index: 2;

}
#sec2 .slider .buttons .buttonItem {
  width:1rem;
  height:1rem;
  background: black;
  border-radius:100%;
  cursor: pointer;
  margin:.5rem 0;
}
#sec2 .slider .buttons .buttonItem.buttonItemActive {
  background: #d82750;
}
