@import url(https://fonts.googleapis.com/css?family=Public+Sans:300,400,700,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:300,400,700&display=swap);
* {
  padding:0;
  margin:0;
  font-family: 'Public Sans', sans-serif;
  box-shadow: none;
}
html {
  -webkit-font-smoothing: antialiased;
}
ul,li,ol {
  list-style: none;
  padding:0;
  margin:0;
}

a {
  text-decoration: none;
  color:black;
}

.korean {
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  opacity:.7;
}
section {
  position: relative;
}

.textwrap {
  position: absolute;
  top:50%;
  left:50%;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
  width:40%;
  -webkit-transition:-webkit-transform .5s ease;
  transition:-webkit-transform .5s ease;
  transition:transform .5s ease;
  transition:transform .5s ease, -webkit-transform .5s ease;
}

.textwrap h1 {
  font-size:2.4rem;
  margin-bottom:2rem;
}
.textwrap p {
  font-size:1.5rem;
  line-height: 180%;
}

.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease;
  transition: opacity 500ms ease;
}

header {
  background: #100f0f;
  position: fixed;
  width:100%;
  z-index: 99;
  border-bottom:.1px solid gray;
}
header ul {
  margin:0 auto;
  width:90%;
  text-align: center;
}

header ul li {
  display: inline-block;
}
header ul li a {
  color:white;
  padding:.6rem 2rem;
  display: inline-block;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.navItem.active {
  pointer-events: none;
  background: #d82750;

}

footer {
  width:100%;
  background: black;
  color:white;
  text-align: center;
}

footer p {
  display: inline-block;
  padding:1rem 1.5rem;
}

#sec2 {
  background: #f2f2f2;
  padding:5rem 0;
}
#sec2 .slider {
  width:60%;
  margin:0rem auto;
  position: relative;
}
#sec2 .slider h2 {
  margin-bottom:3rem;
  text-align: center;
}
#sec2 .slider ul {
  position: relative;
  font-size:0;
  box-shadow:0 5px 10px #cccccc;
}
#sec2 .slider ul li {
  display: inline-block;
  -webkit-transition:opacity .5s ease;
  transition:opacity .5s ease;
  opacity:0;
  position: relative;
  position: absolute;
  width:100%;
}
#sec2 .slider ul li.imgActive {
  opacity:1;
}
#sec2 .slider ul li img {
  width:100%;
}
#sec2 .slider .buttons {
  text-align: center;
  margin-top:1.5rem;
  position: absolute;
  top:50%;
  right:0%;
  -webkit-transform:translate(300%,-50%);
          transform:translate(300%,-50%);
  z-index: 2;

}
#sec2 .slider .buttons .buttonItem {
  width:1rem;
  height:1rem;
  background: black;
  border-radius:100%;
  cursor: pointer;
  margin:.5rem 0;
}
#sec2 .slider .buttons .buttonItem.buttonItemActive {
  background: #d82750;
}

#sec1 {
  position: relative;
  overflow: hidden;
}

#sec1 video {
  width:100%;
}

#sec1 .textwrap.appearActive {
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
}
#sec1 .textwrap.appear {
  -webkit-transform:translate(-50%,0%);
          transform:translate(-50%,0%);
}

#sec3 {
  height:90vh;
  background:url(../../static/media/b2.60b69a0e.png) no-repeat 50% 50%;
  background-size:cover;
}

#sec3 .textwrap {
  width:60%;
  text-align: center;
}

#sec3 .textwrap figure {
  background: #100f0f;
  color:white;
  display: inline-block;
  font-size:4rem;
  width:150px;
  height:150px;
  text-align: center;
  line-height: 150px;
  position: relative;
  border-radius:20px;
  margin:3rem 1rem;
}
#sec3 .textwrap figure img {
  width:70%;
  position: absolute;
  top:50%;
  left:50%;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
}
#sec3 .textwrap figure figcaption {
  width:300%;
  color:black;
  font-size:1.5rem;
  position: absolute;
  bottom:-40%;
  left:50%;
  -webkit-transform:translate(-50%);
          transform:translate(-50%);
  line-height:normal;
}
span.hidden {
  opacity:0;
}

#sec4 {
  padding:10rem 0;
}
#sec4 .row {
  margin-bottom:8rem;
}
#sec4 .cell {
  width:50%;
  display: inline-block;
}
#sec4 .textwrap {
  position: relative;
  -webkit-transform:translate(0,0);
          transform:translate(0,0);
  left:0;
  top:0;
  float:right;
}
#sec4 .textContain {
  position: absolute;
  top:50%;
  left:0%;
  -webkit-transform:translate(5%,-50%);
          transform:translate(5%,-50%);
  width:80%;
}
#sec4 .textwrap h3{
  font-size:2rem;
  margin-bottom:2rem;
}
#sec4 .textwrap p{
  font-size:1.2rem;
}
#sec4 .textwrap a{
  display: inline-block;
  font-size:2rem;
  margin-top:2rem;
  text-decoration: underline;
  color:#d82750;
  font-weight: bold;
}
#sec4 .imgwrap {
  position: relative;
}
#sec4 .imgwrap img{
  position: absolute;
  top:50%;
  right:0%;
  -webkit-transform:translate(-5%,-50%);
          transform:translate(-5%,-50%);
  width:80%;
  box-shadow:0 5px 10px #cccccc;
}

#sec5 {
  width:100%;
}
#sec5 .sectionHeader {
  width:100%;
  height:400px;
  background: #100f0f;
  text-align: center;
  position: relative;
  color:white;
}
#sec5 .sectionHeader h2 {
  font-size:3rem;
  margin-bottom:2rem;
}

.marginBlock {
  height:2rem;
  background: #100f0f;
}
#sec5 article {
  width:50%;
  margin:0 auto;
  padding:12rem 0;
}
#sec5 article .textwrap {
  color:#100f0f;
  position: static;
  -webkit-transform:translate(0,0);
          transform:translate(0,0);
  width:100%;
}

#sec5 article .textwrap h2 {
  font-size:2.4rem;
  margin-bottom:.6rem;
  opacity:1;
}
#sec5 article .textwrap h3 {
  color:#d82750;
  opacity:1;
  font-size:1.8rem;
  margin-bottom:4rem;
}
#sec5 article .textwrap p {
  font-size:1.4rem;
  margin-bottom:4rem;
}
#sec5 article .textwrap h4 {
  font-size:1.4rem;
  margin-bottom:2rem;
  margin-bottom:4rem;
}

