header {
  background: #100f0f;
  position: fixed;
  width:100%;
  z-index: 99;
  border-bottom:.1px solid gray;
}
header ul {
  margin:0 auto;
  width:90%;
  text-align: center;
}

header ul li {
  display: inline-block;
}
header ul li a {
  color:white;
  padding:.6rem 2rem;
  display: inline-block;
  transition: all .3s ease;
}
.navItem.active {
  pointer-events: none;
  background: #d82750;

}
