#sec5 {
  width:100%;
}
#sec5 .sectionHeader {
  width:100%;
  height:400px;
  background: #100f0f;
  text-align: center;
  position: relative;
  color:white;
}
#sec5 .sectionHeader h2 {
  font-size:3rem;
  margin-bottom:2rem;
}

.marginBlock {
  height:2rem;
  background: #100f0f;
}
#sec5 article {
  width:50%;
  margin:0 auto;
  padding:12rem 0;
}
#sec5 article .textwrap {
  color:#100f0f;
  position: static;
  transform:translate(0,0);
  width:100%;
}

#sec5 article .textwrap h2 {
  font-size:2.4rem;
  margin-bottom:.6rem;
  opacity:1;
}
#sec5 article .textwrap h3 {
  color:#d82750;
  opacity:1;
  font-size:1.8rem;
  margin-bottom:4rem;
}
#sec5 article .textwrap p {
  font-size:1.4rem;
  margin-bottom:4rem;
}
#sec5 article .textwrap h4 {
  font-size:1.4rem;
  margin-bottom:2rem;
  margin-bottom:4rem;
}
