#sec4 {
  padding:10rem 0;
}
#sec4 .row {
  margin-bottom:8rem;
}
#sec4 .cell {
  width:50%;
  display: inline-block;
}
#sec4 .textwrap {
  position: relative;
  transform:translate(0,0);
  left:0;
  top:0;
  float:right;
}
#sec4 .textContain {
  position: absolute;
  top:50%;
  left:0%;
  transform:translate(5%,-50%);
  width:80%;
}
#sec4 .textwrap h3{
  font-size:2rem;
  margin-bottom:2rem;
}
#sec4 .textwrap p{
  font-size:1.2rem;
}
#sec4 .textwrap a{
  display: inline-block;
  font-size:2rem;
  margin-top:2rem;
  text-decoration: underline;
  color:#d82750;
  font-weight: bold;
}
#sec4 .imgwrap {
  position: relative;
}
#sec4 .imgwrap img{
  position: absolute;
  top:50%;
  right:0%;
  transform:translate(-5%,-50%);
  width:80%;
  box-shadow:0 5px 10px #cccccc;
}
