@import url('https://fonts.googleapis.com/css?family=Public+Sans:300,400,700,800&display=swap');
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR:300,400,700&display=swap');
* {
  padding:0;
  margin:0;
  font-family: 'Public Sans', sans-serif;
  box-shadow: none;
}
html {
  -webkit-font-smoothing: antialiased;
}
ul,li,ol {
  list-style: none;
  padding:0;
  margin:0;
}

a {
  text-decoration: none;
  color:black;
}

.korean {
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  opacity:.7;
}
section {
  position: relative;
}

.textwrap {
  position: absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  width:40%;
  transition:transform .5s ease;
}

.textwrap h1 {
  font-size:2.4rem;
  margin-bottom:2rem;
}
.textwrap p {
  font-size:1.5rem;
  line-height: 180%;
}
